@import url(https://fonts.googleapis.com/css?family=Roboto:400,400i,500,500i,700,700i&subset=cyrillic-ext);
body {
	margin: 0;
	padding: 0;

	background-color: #ededed;

	font-family: 'Roboto', sans-serif;
}
.container {
    width: 80%;
    max-width: 1280px;
    margin: 0 auto;
}

.header,
.footer {
	padding: 2% 0;
}

.main {
	padding: 2%;
}

.footer {
	background-color: #666;

	text-align: center;
	color: #999;
}
.navigation {
    background-color: #333;
}

.navigation ul {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin: 0;
}

.navigation li {
	display: inline-block;
}

.navigation li:hover {
    background-color: #666;
}

.navigation a {
	display: inline-block;
    padding: 15px 20px;
    color: #fff;

    text-decoration: none;
    text-transform: uppercase;
}
